const useStyles = makeStyles(modalStyle);

export function CheckoutModal(props) {
    const { checkoutModal, handleCheckoutModal, handleNext, handleBack, handleReturn } = props
    const classes = useStyles();


    const Transition = React.forwardRef(function Transition(props, ref) {
        return <Slide direction="down" ref={ref} {...props} />;
    });

    Transition.displayName = "Transition";

    return (
        <Dialog
            classes={{
                root: classes.center,
                paper: classes.modal
            }}
            open={checkoutModal}
            // TransitionComponent={Transition}
            keepMounted
            onClose={() => handleCheckoutModal(false)}
            aria-labelledby="classic-modal-slide-title"
            aria-describedby="classic-modal-slide-description"
        >
            <DialogTitle
                id="classic-modal-slide-title"
                disableTypography
                className={classes.modalHeader}
            >
                <IconButton
                    className={classes.modalCloseButton}
                    key="close"
                    aria-label="Close"
                    color="inherit"
                    onClick={() => handleCheckoutModal(false)}
                >
                    <Close className={classes.modalClose} />
                </IconButton>
                <h4 className={classes.modalTitle}>Review your package order before proceeding to confirmation</h4>
            </DialogTitle>
            <DialogContent
                id="classic-modal-slide-description"
                className={classes.modalBody}
            >
                <GridContainer justify="center">
                    <div>
                        <h4 className={classes.bold}>PACKAGES</h4>
                        <p>1 x 15 pages album</p>
                        <p>10 x 4R photos</p>
                        <p>10 x digital print</p>

                        <h4 className={classes.bold} >SPECIAL PACKAGES</h4>
                        <p>10 x 5R photos</p>
                        <p>20 x digital print</p>
                    </div>
                </GridContainer>
            </DialogContent>
            <DialogActions className={classes.modalFooterCenter}>
                <GridContainer justify="center">
                    <GridItem>
                        <div>
                            <Button
                                color="pink"
                                round size={"lg"}
                                onClick={() => handleNext(false)}
                            >  Proceed to Confirmation </Button>
                        </div>
                        <div style={{ textAlign: 'center' }}>
                            <Button
                                onClick={() => handleCheckoutModal(false)}
                                color="transparent"
                                simple
                                size={"lg"}
                            > Back  </Button>
                        </div>
                    </GridItem>
                </GridContainer>
            </DialogActions>
        </Dialog>
    )
}

function FavoritePhotos(props) {
    const { images, classes, navImageClasses, handleDoubleClick } = props
    return (
        <GridContainer justify="center" spacing={4} style={{ color: 'black' }}>
            {images.filter(like => !like.liked === images.length)}
            {images.length > 0 ? images.filter(like => like.liked).map(img => (

                <div style={{ width: '20%', marginRight: 8 }}>
                    <Card>
                        <div >
                            <img
                                alt="..."
                                src={img.image}
                                className={navImageClasses}
                            />
                        </div>
                    </Card>
                </div>
            )) : null
            }
            {
                images.filter(like => like.liked).length === 0 ? <h4 className={classes.title, classes.marginTop}>NO PHOTOS SELECTED</h4> : null
            }


        </GridContainer>
    )
}

