export default function SelectPhoto(props) {
    const {
        rest,
        classes,
        activeStep,
        steps,
        images,
        navImageClasses,
        handleDoubleClick,
        handleBack,
        handleReset,
        handleNext,
        isStepOptional,
        handleSkip,
        handleReturn,
        classNames,
        // setCheckoutModal,
        checkoutModal,
        handleCheckoutModal
    } = props;

    return (
        <div>

            <div className={classes.container}>
                <GridItem>
                    <CheckoutModal
                        classes={classes}
                        handleCheckoutModal={handleCheckoutModal}
                        checkoutModal={checkoutModal}
                        handleReturn={handleReturn}
                        handleNext={handleNext}
                        handleBack={handleBack}
                    />
                </GridItem>

                {/* <div className={classNames(classes.main, classes.mainRaised)}> */}
                <GridContainer justify="center">
                    <GridItem xs={12} sm={12} md={6}>
                        <GridContainer justify="center">
                            <div className={classes.name}>
                                <h2 className={classes.title}>Select Photos</h2>
                                <h4 className={classes.subTitle}>Select photos for your package</h4>
                            </div>
                        </GridContainer>

                        <div className={classes.root}>
                            <StepperComponent activeStep={activeStep} steps={steps} classes={classes} handleReturn={handleReturn} />
                        </div>

                    </GridItem>

                </GridContainer>
                <GridContainer justify="center" >
                    {/* <GridItem xs={12} sm={12} md={8} lg={6}> */}
                    <NavPills
                        alignCenter
                        color="info"
                        tabs={[
                            {
                                tabButton: "Favorites",
                                tabIcon: Favorite,
                                tabContent: (
                                    <FavoritePhotos
                                        images={images}
                                        classes={classes}
                                        navImageClasses={navImageClasses}
                                        handleDoubleClick={handleDoubleClick}
                                    />
                                )
                            },
                            {
                                tabButton: "All Photos",
                                tabIcon: PhotoCamera,
                                tabContent: (
                                    <AllPhotos
                                        images={images}
                                        classes={classes}
                                        navImageClasses={navImageClasses}
                                        handleDoubleClick={handleDoubleClick}
                                    />
                                )
                            },
                        ]}
                    />
                    {/* </GridItem> */}
                </GridContainer>


            </div>



            <Footer
                fixed
                activeStep={activeStep}
                steps={steps}
                handleBack={handleBack}
                handleReset={handleReset}
                handleNext={handleNext}
                total={images ? images.length : 0}
                liked={Object.values(images).reduce((a, { liked }) => a + liked, 0)}
                isStepOptional={isStepOptional}
                handleSkip={handleSkip}
                handleCheckoutModal={handleCheckoutModal}
            />

        </div>
        // </div>
    );
}