import SpecialPackage from "./SpecialPage";
import FavoritePhoto from "./FavoritePage";
import SelectPhoto from "./SelectPhotoPage";
import Confirmation from "./ConfirmationPage";


const useStyles = makeStyles(styles);

function getSteps() {
  return ['Favorite Photo', 'Special Package', 'Select Photo', 'Confirmation', 'Payment'];
}

function getImages() {
  return [
    { image: work1, index: 0, liked: false },
    { image: work2, index: 1, liked: false },
    { image: work3, index: 2, liked: false },
    { image: work4, index: 3, liked: false },
    { image: work5, index: 4, liked: false },
    { image: work1, index: 5, liked: false },
    { image: work2, index: 6, liked: false },
    { image: work3, index: 7, liked: false },
    { image: work4, index: 8, liked: false },
    { image: work5, index: 9, liked: false },
    { image: work1, index: 10, liked: false },
    { image: work2, index: 11, liked: false },
    { image: work3, index: 12, liked: false },
    { image: work4, index: 13, liked: false },
    { image: work5, index: 14, liked: false },
    { image: work1, index: 15, liked: false },
  ];
}

export default function HomePage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  const [activeStep, setActiveStep] = React.useState(0);
  const [click, setClick] = React.useState(false)
  const [skipped, setSkipped] = React.useState(new Set());
  const [images, setImages] = React.useState(getImages());
  const [checkoutModal, setCheckoutModal] = React.useState(false);
  const steps = getSteps();

  const isStepOptional = step => {
    return step === 1;
  };

  const isStepSkipped = step => {
    return skipped.has(step);
  };

  const handleDoubleClick = (img) => {
    const curImg = [...images]
    if (img.liked) curImg[img.index].liked = false
    else curImg[img.index].liked = true
    setImages(curImg);
  }

  const handleNext = (modal = true) => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }
    if(!modal) setCheckoutModal(false) 

    setActiveStep(prevActiveStep => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  // const modalCondition = () => { if (activeStep === 3) setCheckoutModal(true) }
  const handleCheckoutModal = () => { 
    if (checkoutModal) setCheckoutModal(false) 
    else if(!checkoutModal) setCheckoutModal(true)
  }

  // useEffect(() => {
  //   // modalCondition();
  // }, [activeStep]);

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  const handleSkip = () => {
    if (!isStepOptional(activeStep)) {
      // You probably want to guard against something like this,
      // it should never occur unless someone's actively trying to break something.
      throw new Error("You can't skip a step that isn't optional.");
    }

    setActiveStep(prevActiveStep => prevActiveStep + 1);
    setSkipped(prevSkipped => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const handleReturn = (index) => {
    if (index < activeStep) setActiveStep(index);
    // if (closeModal) setCheckoutModal(false)
    else return
  };

  setTimeout(function () {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  const { ...rest } = props;
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid
  );
  const navImageClasses = classNames(classes.imgRounded, classes.imgGallery);
  return (
    <div>
      <Header
        absolute
        color="tr"
        fixed
        rightLinks={<HeaderLinks />}
        {...rest}
      />

      <div
        className={classes.pageHeader}
        style={{
          backgroundImage: "url(" + image + ")",
          backgroundSize: "cover",
          backgroundPosition: "top center"
        }}
      >

        {activeStep === 0 ?
          <FavoritePhoto
            rest={rest}
            classes={classes}
            activeStep={activeStep}
            steps={steps}
            images={images}
            navImageClasses={navImageClasses}
            handleDoubleClick={handleDoubleClick}
            handleBack={handleBack}
            handleReset={handleReset}
            handleNext={handleNext}
            isStepOptional={isStepOptional}
            handleSkip={handleSkip}
            handleReturn={handleReturn}
          />
          : null}

        {activeStep === 1 ?
          <SpecialPackage
            rest={rest}
            classes={classes}
            activeStep={activeStep}
            steps={steps}
            images={images}
            navImageClasses={navImageClasses}
            handleDoubleClick={handleDoubleClick}
            handleBack={handleBack}
            handleReset={handleReset}
            handleNext={handleNext}
            isStepOptional={isStepOptional}
            handleSkip={handleSkip}
            handleReturn={handleReturn}
          />
          : null}

        {activeStep === 2 ?
          <SelectPhoto
            rest={rest}
            classes={classes}
            activeStep={activeStep}
            steps={steps}
            images={images}
            navImageClasses={navImageClasses}
            handleDoubleClick={handleDoubleClick}
            handleBack={handleBack}
            handleReset={handleReset}
            handleNext={handleNext}
            isStepOptional={isStepOptional}
            handleSkip={handleSkip}
            handleReturn={handleReturn}
            classNames={classNames}
            setCheckoutModal={setCheckoutModal}
            checkoutModal={checkoutModal}
            handleCheckoutModal={handleCheckoutModal}
          />
          : null}

          {activeStep === 3 ?
          <Confirmation
            rest={rest}
            classes={classes}
            activeStep={activeStep}
            steps={steps}
            images={images}
            navImageClasses={navImageClasses}
            handleDoubleClick={handleDoubleClick}
            handleBack={handleBack}
            handleReset={handleReset}
            handleNext={handleNext}
            isStepOptional={isStepOptional}
            handleSkip={handleSkip}
            handleReturn={handleReturn}
            classNames={classNames}
            setCheckoutModal={setCheckoutModal}
            checkoutModal={checkoutModal}
            handleCheckoutModal={handleCheckoutModal}
          />
          : null}
      </div>

    </div>
  );
}
